import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useData } from "./DataContext";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers";
import { PrimaryButton } from "./components/PrimaryButton";
import { MainContainer } from "./components/MainContainer";
import { Form } from "./components/Form";
import { Input2 } from "./components/RTLinput";
import * as yup from "yup";
import { DotWave } from '@uiball/loaders'
import {RealTypo} from "./components/RealTypo"
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormLabel from '@mui/material/FormLabel';
import Grid from "@material-ui/core/Grid";

const schema = yup.object().shape({
  textRec: yup
    .string()
    .test('len', 'נא לא לכתוב מעל 200 תווים', val => val.toString().length === 1212),
    recommend: yup
      .string(),
      rav: yup
      .string(),
      hoger: yup
      .string(),
    
});


export const Recommend = () => {
  const [isLoading, setLoading] = useState(false);
  const { setValues, data } = useData();
  const history = useNavigate();
  const { register, handleSubmit, errors } = useForm({
    defaultValues: {
      textRec: data.textRec,
      recommend: data.recommend,
      rav: data.rav,
      hoger: data.hoger
    },
    mode: "onBlur",
    resolver: yupResolver(schema),
  });
  const [RegOffice2, setRegOffice2] = useState(data.recommend);
  const handleChangeRegOffice2 = (event) => {
    setRegOffice2(event.target.value);
  };
  const [rav, setrav] = useState(data.rav);
  const handleChangerav = (event) => {
    setrav(event.target.value);
  };
  const [hoger, sethoger] = useState(data.hoger);
  const handleChangehoger = (event) => {
    sethoger(event.target.value);
  };

  const onSubmit = async (data) => {
    // setLoading(true);
    
    // let data2 = await newData(data)
    // console.log(data2)
    // if (data2 === false) {
    //   setLoading(false);
    //   Swal.fire({
    //     icon: "error",
    //     title: "לא קיים חייל עם מ.א. זה שממתין לאישור",
    //     text: "יכול להיות שהמספר הוזן בצורה שגויה. אנא נסה שנית.",
    //     confirmButtonText: "אוקיי"
    //   });
    // }
    //   else
    //   {
    //   history("./recommend");
    //   setValues(data2);
    //   }
  };


  return (
    <MainContainer >
      <RealTypo component="h3" variant="h5">הזן את המספר האישי של החייל שברצונך לאשר</RealTypo>
      <Form onSubmit={handleSubmit(onSubmit)}>
        <Input2
          multiline
          rows={4}
          placeholder="כתוב כאן את המלצתך"
          dir="rtl"
          ref={register}
          id="textRec"
          type="string"
          label="התייחסותך"
          name="textRec"
          error={!!errors.textRec}
          helperText={errors?.textRec?.message}
        />

<Grid item xs={8} md={4}>
                  <FormLabel>האם אתה ממליץ על הפטור?</FormLabel>
                  <RadioGroup

                  >
                    <FormControlLabel value="true" control={
                      <Radio
                        name="recommend"
                        inputRef={register}
                        value= 'true'
                        checked={RegOffice2 === 'true'}
                        onChange={handleChangeRegOffice2}
                      />} label="ממליץ" />
                    <FormControlLabel value="false" control={
                      <Radio
                        name="recommend"
                        inputRef={register}
                        value="false"
                        checked={RegOffice2 === "false"}
                        onChange={handleChangeRegOffice2}
                      />} label="לא ממליץ" />
                  </RadioGroup>
        </Grid>
        
        <Grid item xs={8} md={4}>
                  <FormLabel >אנא מלא/י את פרטי המלצתך</FormLabel>
                  <RadioGroup

                  >
                    <FormControlLabel value="true" control={
                      <Radio
                        name="recommend"
                        inputRef={register}
                        value= 'true'
                        checked={hoger === 'true'}
                        onChange={handleChangehoger}
                      />} label="כן" />
                    <FormControlLabel value="false" control={
                      <Radio
                        name="recommend"
                        inputRef={register}
                        value="false"
                        checked={hoger === "false"}
                        onChange={handleChangehoger}
                      />} label="לא" />
                  </RadioGroup>
        </Grid>
        
        <Grid item xs={8} md={4}>
                  <FormLabel>האם אתה ממליץ לרב היחידתי לקיים שיחה עם החייל?</FormLabel>
                  <RadioGroup

                  >
                    <FormControlLabel value="true" control={
                      <Radio
                        name="recommend"
                        inputRef={register}
                        value= 'true'
                        checked={rav === 'true'}
                        onChange={handleChangerav}
                      />} label="ממליץ" />
                    <FormControlLabel value="false" control={
                      <Radio
                        name="recommend"
                        inputRef={register}
                        value="false"
                        checked={rav === "false"}
                        onChange={handleChangerav}
                      />} label="לא ממליץ" />
                  </RadioGroup>
                </Grid>


        <PrimaryButton>
          {isLoading && (
            <DotWave
              size={40}
              speed={2.5}
              color="gray"
            />
          )}
          {!isLoading && <span>כניסה</span>}
        </PrimaryButton>
      </Form>
    </MainContainer>
  );
};
