import React, { useState } from "react";
import Confetti from "react-confetti";
import Swal from "sweetalert2";
import { Link } from "react-router-dom";
import { useData } from "./DataContext";
import { MainContainer } from "./components/MainContainer";
import { PrimaryButton } from "./components/PrimaryButton";
import Typography from "@material-ui/core/Typography";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import SaveAltIcon from '@mui/icons-material/SaveAlt';
import BackupIcon from '@mui/icons-material/Backup';
import ListItemText from "@material-ui/core/ListItemText";
import InsertDriveFile from "@material-ui/icons/InsertDriveFile";
import { SecondryButton } from "./components/SecondryButton";
import { collection, addDoc, setDoc, doc, where, getDocs, query } from "firebase/firestore";
import {db,storage} from "./base.js"
import { PDFDocument, ReadingDirection } from 'pdf-lib'
import download from "downloadjs";
import fontkit from '@pdf-lib/fontkit';
import { ref,getDownloadURL, uploadBytesResumable } from "firebase/storage";
import { useNavigate } from "react-router-dom";
import {RealTypo} from "./components/RealTypo"



export const Result = () => {
  const [success, setSuccess] = useState(false);
  const [setProgress] = useState(0);
  const { data } = useData();
  const entries = Object.entries(data).filter((entry) => entry[0] !== "files");
  const { files } = data;
  const collectionRef = collection(db, "Members")




  const flattenForm = async () => {
    // Fetch the PDF with form fields
    const formUrl = 'https://firebasestorage.googleapis.com/v0/b/perekmesimaapp.appspot.com/o/readyExcellentPtor.pdf?alt=media&token=40311b41-9205-491c-94d6-d22bccd5ba94';
    const marioUrl = 'https://pdf-lib.js.org/assets/small_mario.png'
    const marioImageBytes = await fetch(marioUrl).then(res => res.arrayBuffer());
    const ImageBytes = await fetch(data["imageData"]).then(res => res.arrayBuffer());

    console.log("url= "+marioUrl);
    console.log("bytes= "+marioImageBytes);

    const formBytes = await fetch(formUrl).then((res) => res.arrayBuffer());
    const pdfDoc = await PDFDocument.load(formBytes);
    console.log(pdfDoc.context.header.toString());
    // Fetch the Ubuntu font
    const fontUrl = 'https://firebasestorage.googleapis.com/v0/b/perekmesimaapp.appspot.com/o/DavidLibre-Regular.ttf?alt=media&token=7fa3b6aa-9f68-4823-9620-55042a0417b5';
    const fontBytes = await fetch(fontUrl).then((res) => res.arrayBuffer());

    ;

    // Load the PDF with form fields


    // Embed the Ubuntu font
    pdfDoc.registerFontkit(fontkit);
    const ubuntuFont = await pdfDoc.embedFont(fontBytes);
    const marioImage = await pdfDoc.embedPng(marioImageBytes);
    const sigFinal = await pdfDoc.embedPng(ImageBytes);

    console.log("image= "+marioImage);
    // Get two text fields from the form
    const form = pdfDoc.getForm();
    const viewerPrefs = pdfDoc.catalog.getOrCreateViewerPreferences()
    viewerPrefs.setReadingDirection(ReadingDirection.R2L)
    const fields = form.getFields()
    fields.forEach(field => {
      const type = field.constructor.name
      const name = field.getName()
      console.log(`${type}: ${name}`)
    })
    const nameField = form.getTextField('firstname');
    const lastnameField = form.getTextField('lastname');
    const idField = form.getTextField('ID');
    const rankField = form.getTextField('RANK');
    const ishyORdati = form.getRadioGroup('Req');
    const shapeOfBeard = form.getRadioGroup('Shape');
    const isDatiField = form.getCheckBox('Checkbox');
    const dateField = form.getTextField('Date');
    const imgField = form.getButton('ImageField');
    const mpExplain = form.getTextField('mpExplain');
    // Fill the text fields with some fancy Unicode characters (outside
    // the WinAnsi latin character set)
    nameField.setText(data["firstName"]);
    lastnameField.setText(data["lastName"]);
    idField.setText(data["personalID"]);
    rankField.setText(data["ranks"]);
    ishyORdati.select(data["datiORishylabel"]);
    shapeOfBeard.select(data["shapeOfBeard"]);
      
    if (data["isDati"])
      isDatiField.check();
    else
      isDatiField.enableReadOnly();
    dateField.setText(data["date"]);
    nameField.enableReadOnly();
    lastnameField.enableReadOnly();
    idField.enableReadOnly();
    rankField.enableReadOnly();
    dateField.enableReadOnly();
    shapeOfBeard.enableReadOnly();
    ishyORdati.enableReadOnly();
    isDatiField.enableReadOnly();
    imgField.setImage(sigFinal);
    mpExplain.setText(`חייל טוב בשורה התחתונה, משקיע ועוזר הרבה כשצריך.
    ממליץ.
    עלההיתר`);
    mpExplain.enableMultiline();
    imgField.enableReadOnly();
    mpExplain.enableReadOnly();
    // **Key Step:** Update the field appearances with the Ubuntu font
    form.updateFieldAppearances(ubuntuFont);
    // const pdfDoc1 = await PDFDocument.load(data["files"])
    // const [pdfDoc1page] = await pdfDoc.copyPages(pdfDoc1, [0])

    // pdfDoc.insertPage(0, pdfDoc1page)

    // Save the PDF with filled form fields
    const metadata = {
      contentType: 'application/pdf',
    };
    
    const pdfBytes = await pdfDoc.save();
    download(pdfBytes, data["personalID"], metadata);
    const storageRef = ref(storage, `/ZakanRequests/${ data["personalID"]}/Request_${data["personalID"]}`);
    const uploadTask2 = uploadBytesResumable(storageRef, pdfBytes, metadata);
    // Trigger the browser to download the PDF document
    uploadTask2.on(
      "state_changed",
      (snapshot) => {
        const prog = Math.round(
          (snapshot.bytesTransferred / snapshot.totalBytes) * 100
        );
        setProgress(prog);
      },
      (error) => console.log(error),
      () => {
        getDownloadURL(uploadTask2.snapshot.ref).then((downloadURL) => {
          console.log("File available at", downloadURL);
        });
      }
    );
    onSubmit();
  }

  const history = useNavigate();
  const idNum = data["personalID"];


  const onSubmit = async () => {
    const formData = new FormData();
    if (data.files) {  
      console.log(idNum);
      // ref(storage,`/ZakanRequests/${filename}/`);
      data.files.forEach((file) => {
        const storageRef1 = ref(storage,`/ZakanRequests/${idNum}/${file.name}`);
        console.log(idNum);
        const uploadTask = uploadBytesResumable(storageRef1, file);
        uploadTask.on(
          "state_changed",
          (snapshot) => {
            const prog = Math.round(
              (snapshot.bytesTransferred / snapshot.totalBytes) * 100
            );
            setProgress(prog);
          },
          (error) => console.log(error),
          () => {
            getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
              console.log("File available at", downloadURL);
            });
          }
        );
      });
    }
    entries.forEach((entry) => {
      formData.append(entry[0], entry[1]);
    });

    var object = {};

    formData.forEach(function (value, key) {
      object[key] = value;
    });

    var objIndex = Object.values(object);
    var keyIndex = Object.keys(object).indexOf('personalID');
    let dupValue = objIndex[keyIndex];

    const q = query(collectionRef, where("personalID", "==", dupValue));
    const querySnapshot = await getDocs(q);
    let a = true;
    querySnapshot.forEach((doc1) => {
      console.log(doc1.id, " => ", doc1.data());
      setDoc(doc(collectionRef, doc1.id), object);
      a = false;
      let timerInterval

      Swal.fire({
        icon: "success",
        title: "מעולה!",
        text: "הפרטים שלך נקלטו במערכת!",
        confirmButtonText: "אוקיי",
        timer: 5000,
        timerProgressBar: true,
        didOpen: () => {
          Swal.showLoading()
          const b = Swal.getHtmlContainer().querySelector('b')
          timerInterval = setInterval(() => {
            b.textContent = Swal.getTimerLeft()
          }, 100)
        },
        willClose: () => {
          clearInterval(timerInterval)
        }
        }
      ).then((result) => {
        if (result.dismiss === Swal.DismissReason.timer) {
          history("/");
        } else {
          if (true)
          {
            history("/");
            }
          // result.dismiss can be 'cancel', 'overlay', 'esc' or 'timer'
        }
      });
      setSuccess(true);
    });
    if (a) { await addDoc(collectionRef, object); }

    /*    const res = await fetch("http://localhost:4000/", {
         method: "POST",
         body: formData,
       });
   
       if (res.status === 200) {
        
       } */
  };
  if (success) {
    return <Confetti
    />;
  }
  /*   
    }
   */
  return (
    <>
      <MainContainer>

        <RealTypo component="h2" variant="h5">
        אליפות! זה הכל!
        </RealTypo>
        <SecondryButton dir='ltr' variant="contained" startIcon={<SaveAltIcon />} onClick={flattenForm}>
        PDF שמור כקובץ
        </SecondryButton>
        <PrimaryButton dir='ltr' variant="contained" startIcon={<BackupIcon />} onClick={onSubmit}>שמור את הפרטים לפעם הבאה</PrimaryButton>
        {/* <TableContainer className={styles.root} component={Paper}>
          <Table className={styles.table} aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell>קטגורייה</TableCell>
                <TableCell align="right">נתונים</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {entries.map((entry) => (
                <TableRow key={entry[0]}>
                  <TableCell component="th" scope="row">
                    {entry[0]}
                  </TableCell>
                  <TableCell align="right">{entry[1].toString()}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer> */}
        {files && (
          <>
            <Typography component="h2" variant="h5">
             מסמכים מצורפים
            </Typography>
            <List>
              {files.map((f, index) => (
                <ListItem key={index}>
                  <ListItemIcon>
                    <InsertDriveFile />
                  </ListItemIcon>
                  <ListItemText primary={f.name} secondary={f.size} />
                </ListItem>
              ))}
            </List>
          </>
        )}
        
        <Link to="/">התחל מחדש</Link>
      </MainContainer>
    </>
  );
};
